.container {
  padding-top: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.grid {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-row: 1fr 1fr 1fr 1fr 1fr 1fr;
  grid-template-areas:
    "AC AC divide multiply"
    "seven eight nine minus"
    "four five six plus"
    "one two three equal"
    "zero zero dot equal";
}

.padButton {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 20px 30px;
  background-color: gray;
  color: white;
  border: 1px solid black;
  cursor: pointer;
  font-size: 20px;
}

.padButton:hover {
  border: 1px solid white;
  color: black;
}

.dark-gray {
  background-color: #474747;
}

.calculator {
  border: 2px solid #47476b;
  padding: 5px;
  background: black;
  width: 320px;
  position: relative;
}

.dis {
  grid-area: dis;
  background-color: black;
  text-align: right;
  height: 60px;
  font-size: 20px;
  border: 2px solid #13131b;
  padding: 5px;
  background: black;
  position: relative;
}

input {
  border: none;
  padding: 5px 5px;
  width: 95%;
}

.dis input {
  color: white;
  text-align: right;
  background-color: black;
  font-family: Digital Numbers Regular;
}

input::placeholder {
  color: white;
}

.total {
  padding: 5px 5px;
  color: yellow;
  font-family: Digital Numbers Regular;
}

@font-face {
  font-family: "Digital Numbers Regular";
  font-style: normal;
  font-weight: normal;
  src: local("Digital Numbers Regular"), url("./fonts/DigitalNumbers-Regular.ttf") format("woff");
}

.tomato {
  background-color: tomato;
}

.blue {
  background-color: #59f;
}

.zero {
  grid-area: zero;
}

#equals {
  grid-area: equal;
}

#clear {
  grid-area: AC;
}

.author {
  text-align: center;
  font-family: Share Tech Mono, sans;
  margin-top: 15px;
}
